import React from 'react';
import style from './Status.module.css'

class Status extends React.Component {
    constructor(props) {
        super(props);
        debugger
    }

    statusClass = this.props.statusOutput === 'textOutput' ? style.textOutput :
        this.props.statusOutput === 'max' ? style.outputMaxValue :
            this.props.statusOutput === 'error' ? style.outputError : '';
               /* style.textOutput*/
    render = () => {
        /*  let */
        /* style.outputPermissible*/
        return (
            <div className={style.OutputScreen}>
                <div className={this.statusClass}>{this.props.outputValue}</div>
            </div>

        )
    }
}

export default Status;